import React from 'react';
import 'semantic-ui-css/semantic.min.css';
import { List, Segment, Header } from 'semantic-ui-react';
import './IngredientList.css';

const ListItem = (props: { content: string }) => (
  <List.Item>
    <List.Content>{props.content}</List.Content>
  </List.Item>
)

interface IProps {
  items: string[]
}
interface IState {}

export class IngredientList extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {};
  }

  render() {
    const content = this.props.items.length === 0
      ? (<Header className="empty-message" as="h5">There's nothing here</Header>)
      : this.props.items.map(item => <ListItem content={item} />);

    return (
      <Segment>
        <List divided ordered relaxed>
          {content}
        </List>
      </Segment>
    )
  }
}