import React, { FormEvent } from 'react';
import './App.css';
import 'semantic-ui-css/semantic.min.css';

import { Container, Icon, Form, Grid, Header, Message, Segment, TextArea } from 'semantic-ui-react';

import { IngredientList } from './modules/IngredientList';
import { compareLists, trimString } from './utils/compare';

const INGREDIENT_REGEX = /([0-9]+.*?[a-zA-Z]+)|[^,]+/g;
const LEADING_SPACE_REGEX = /,[ ]+/;

// AppState
interface IProps {}
interface IState {
  firstTextValue: string;
  secondTextValue: string;
}
class App extends React.Component<IProps, IState> {
  constructor(props: IProps) {
    super(props);

    this.state = {
      firstTextValue: '',
      secondTextValue: ''
    }
  }

  handleFirstTextChanged(event: any) {
    this.setState({ firstTextValue: event.target?.value });
  }

  handleSecondTextChanged(event: any) {
    this.setState({ secondTextValue: event.target?.value });
  }

  render() {
    const listA = (this.state.firstTextValue
      .split('\n').join(',') // Handle Multi-line
      .replace(LEADING_SPACE_REGEX, ',') // Tidy up spaces
      .match(INGREDIENT_REGEX) || []).map(trimString);
    const listB = (this.state.secondTextValue
      .replace(LEADING_SPACE_REGEX, ',')
      .match(INGREDIENT_REGEX) || []).map(trimString);
    const { intersection, onlyInA, onlyInB } = compareLists(listA, listB);

    return (
      <Container style={{ paddingTop: '1rem' }}>
        <Grid columns={2}>
          <Grid.Row>
            <Header as="h1" textAlign="center" style={{ marginLeft: '10px' }}>
              <Icon name="gift" style={{ color: "#6e8ab5" }}/>
              <Header.Content>
                Compare Ingredients
                </Header.Content>
            </Header>
          </Grid.Row>
          <Grid.Row>
            <Grid.Column>
              <Form>
                <Header as="h3">Text A</Header>
                <TextArea
                  onChange={this.handleFirstTextChanged.bind(this)}
                  name="firstTextValue"
                  value={this.state.firstTextValue}
                  style={{ resize: 'none', height: '180px', backgroundColor: '#f7f7f7' }}
                  placeholder='A' />
              </Form>
            </Grid.Column>
            <Grid.Column>
              <Form>
              <Header as="h3">Text B</Header>
                <TextArea
                  onChange={this.handleSecondTextChanged.bind(this)}
                  name="secondTextValue"
                  value={this.state.secondTextValue}
                  style={{ resize: 'none', height: '180px', backgroundColor: '#f7f7f7' }}
                  placeholder='B' />
              </Form>
            </Grid.Column>
          </Grid.Row>
          </Grid>
          <Grid columns={3}>
          <Grid.Row>
            <Grid.Column>
              <Header as="h3">In A and B</Header>
              <IngredientList items={intersection.sort()}></IngredientList>
            </Grid.Column>
            <Grid.Column>
              <Header as="h3">Only in A</Header>
              <IngredientList items={onlyInA.sort()}></IngredientList>
            </Grid.Column>
            <Grid.Column>
              <Header as="h3">Only in B</Header>
              <IngredientList items={onlyInB.sort()}></IngredientList>
            </Grid.Column>

          </Grid.Row>
        </Grid>
      </Container>
    );
  }
}

export default App;
