interface Comparison {
  intersection: string[];
  onlyInA: string[];
  onlyInB: string[];
}

const WeakString = (s: string): string => s.toLowerCase().replace(' ', '');

export function compareLists(listA: string[], listB: string[]): Comparison {
  if (listA.length === 0 || listB.length ===0)
    return { intersection: [], onlyInA: [], onlyInB: [] }

  // const setA = new Set(listA.map(s => WeakString(s)));
  const setB = new Set(listB.map(s => WeakString(s)));
  const intersection = listA.filter(x => setB.has(WeakString(x)));
  const intersectionSet = new Set(intersection.map(x => WeakString(x)));

  const onlyInA = listA.filter(x => !intersectionSet.has(WeakString(x)));
  const onlyInB = listB.filter(x => !intersectionSet.has(WeakString(x)));

  return { intersection, onlyInA, onlyInB }
}

export function trimString(s: string) {
  return s.trim();
}